.photoGallery {
  padding: 20px;
  margin: auto; }
  .photoGallery .message h1 {
    margin-bottom: 50px; }
  .photoGallery .message .photoGalleryIcon {
    font-size: 30px;
    padding-right: 20px; }
  .photoGallery #photoGalleryCarousel .row img {
    padding-bottom: 10px; }
  .photoGallery #photoGalleryCarousel .carousel-inner {
    width: 90%;
    margin: auto; }
  .photoGallery #photoGalleryCarousel .carousel-control {
    width: 5%;
    background-image: none; }
  .photoGallery #photoGalleryCarousel .carousel-indicators {
    display: none; }



