.marquee {
  width: 100%;
  min-height: 170px;
  overflow: hidden;
  background: #57B404; }

.marquee p {
  text-indent: 0;
  position: absolute;
  padding: 30px;
  -webkit-animation: marquee 10s linear infinite;
          animation: marquee 10s linear infinite; }

.marquee p:hover {
  -webkit-animation-play-state: paused;
          animation-play-state: paused; }

@-webkit-keyframes marquee {
  from {
    top: 135px; }
  to {
    top: 35px; } }

@keyframes marquee {
  from {
    top: 135px; }
  to {
    top: 35px; } }



